<template>
    <div>
      <el-container>
        <el-aside width="60px">
          <div>
            <span>工具</span>
            <el-radio-group v-model="selectBtn">
              <el-radio-button label="select" type="primary"  border size="small" icon="el-icon-position" @click.native="changeBtn('select')"><i class="el-icon-position"></i></el-radio-button>
              <el-radio-button label="edit" type="primary"  border size="small" icon="el-icon-rank" @click.native="changeBtn('edit')"><i class="el-icon-rank"></i></el-radio-button>


            </el-radio-group>

          </div>
        </el-aside>
        <el-main style="display: flex;align-items: flex-start;padding:0px;margin: 0px;">
          <canvas width="1000" height="1000" id="canvas"></canvas>
        </el-main>
      </el-container>



      <el-dialog  :visible.sync="dialogFormVisible1"
                 :close-on-press-escape="false"
                 :close-on-click-modal="false"
                 :show-close="true"

      >
        <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
          <span  >{{ (this.currentNode!==undefined?this.currentNode.text:"")}}</span>
          <span  >{{ (this.currentContainer!==undefined?this.currentContainer.text:"")}}</span>
        </div>

      </el-dialog>
    </div>
</template>

<script>
export default {
  name: "JTopoExample",
  mounted:function(){
    this.initTopo();
  },
  data(){
    return{
      selectBtn:"select",
      sceneModel : "nodrag",//"edit"，“select”，“nodrag”
      currentNode:undefined,
      currentContainer:undefined,
      dialogFormVisible1:false,
      publicScene :null,
    }
  },
  methods:{
    changeBtn(flag){
      let that = this;
      if(flag==='edit'){
        that.selectBtn = "edit";
        // that.sceneModel="edit";
        that.publicScene.mode = "edit";
      }else if(flag==='select'){
        that.selectBtn = "select";
        // that.sceneModel = "select";
        that.publicScene.mode = "select";
      }
    },
    //用jtopo画图
    initTopo() {
      let that = this;
      let canvas = document.getElementById("canvas");
      let stage = new JTopo.Stage(canvas);
      stage.eagleEye.visible = true;
      // stage.wheelZoom = 1.2; //缩放比例
      let scene = new JTopo.Scene(stage);
      // scene.background = '/static/bg.png';
      scene.alpha = 1;
      scene.backgroundColor = '63,74,89';
      //例如：stage.mode = "drag";
      //normal[默认]：可以点击选中单个节点（按住Ctrl可以选中多个），点中空白处可以拖拽整个画面
      //drag: 该模式下不可以选择节点，只能拖拽整个画面
      //nodrag :不允许拖拽
      //select: 可以框选多个节点、可以点击单个节点
      //edit: 在默认基础上增加了：选中节点时可以通过6个控制点来调整节点的宽、高
      scene.mode= that.sceneModel;//
      // scene.areaSelect = false;//是否允许多选
      var node = new JTopo.Node("huangwei");
      node.fillColor = '0,0,255';
      // node.setLocation(409, 269);
      //node.setImage('/static/bg.png', true);
      node.setLocation(80, 80);
      node.setSize(32, 32);//大小
      node.text="hw";
      node.textOffsetY = -37; // 文本偏移量（向上37像素）
      node.myType = "device";    // 在创建节点的时候添加自定义属性
      node.serializedProperties.push("myType"); // 把自定义属性加入进节点的属性组 serializedProperties 中
      node.alarm = '二级告警';
      node.alarmColor = '0,255,0';

      node.shadow = true;//阴影
      node.shadowColor = '0,0,255';
      node.shadowOffsetX = 3;
      node.shadowOffsetY = 6;

      node.borderWidth = 1; // 边框的宽度
      node.borderColor = JTopo.util.randomColor(); //边框颜色

      scene.add(node);

      var circleNode = new JTopo.CircleNode('node');
      circleNode.radius = 24; // 半径
      circleNode.alpha = 0.7;
      circleNode.fillColor = '0, 0, 255'; // 填充颜色
      circleNode.setLocation(350, 400);

      // ['Top_Left', 'Top_Center', 'Top_Right', 'Middle_Left', 'Middle_Center', 'Middle_Right', 'Bottom_Left', 'Bottom_Center', 'Bottom_Right'];
      circleNode.textPosition = 'Middle_Center'; // 文本位置
      scene.add(circleNode);

      this.createlinkNode(node,circleNode,'上行',scene);

      var textNode = new JTopo.TextNode('This is a text node.');
      textNode.font = 'bold 16px 微软雅黑';
      textNode.setLocation(317, 250);
      scene.add(textNode);

      var linkNode = new JTopo.LinkNode('超链接：http://www.jtopo.com');
      linkNode.href = 'http://www.jtopo.com';
      linkNode.target = '_blank'; // 新窗口打开链接
      linkNode.font = 'italic bold 16px 微软雅黑';
      linkNode.visitedColor = '0,0,255'; // 访问过的链接为蓝色
      linkNode.shadowOffsetX = 5; // 阴影设置
      linkNode.shadowOffsetY = 16;
      linkNode.setLocation(250, 360);
      scene.add(linkNode);

      var pieNode = new JTopo.PieChartNode();
      pieNode.shadow = false;
      pieNode.radius = 100;
      pieNode.colors = ["#3666B0","#2CA8E0","#77D1F6"];
      pieNode.datas = [0.3, 0.3, 0.4];
      pieNode.titles = ['A', 'B', 'C'];
      pieNode.setLocation(300, 203);

      this.createNode("动态",10,10,32,32,scene);

      scene.add(pieNode);

      node.dbclick(function(event){
        console.log("双击："+ event.target.myType );
        // console.log("双击："+ JSON.stringify(event.target) );
      });

      this.createContainer(10,10,180,90,"19.1",scene);
      // 将画布内容居中
      // stage.centerAndZoom();
      that.publicScene = scene;

    },
    createNode(text,x,y,w,h,scene){
      var that = this;
      let node = new JTopo.Node(text);
      // var node = new JTopo.CircleNode(text);
      // var node = new JTopo.TextNode(text);
      // var node = new JTopo.LinkNode(text);
      // var node = new JTopo.PieChartNode(text);
      node.setLocation(x, y);
      node.setSize(w, h);//大小
      // node.showSelected = true;
      node.showSelected = false; // 不显示选中矩形
      node.paintSelected = function (a){	//修改节点选中样式
        0 != this.showSelected &&
        (a.save(), a.beginPath(), a.strokeStyle = "rgba(218,221,221, 0)",
          a.fillStyle = "rgba(218,221,221,0)",
          a.arc( 0, 0, this.width/2 + 3, 0, Math.PI*2, false),
          a.fill(), a.stroke(), a.closePath(), a.restore())
      };

      node.addEventListener("dbclick", function(event){
        node.dragable = !node.dragable;//是否允许拖动。
        that.currentNode = event.target;
        that.currentContainer = undefined;
        console.info("text："+that.currentNode.text);
        // that.$message({
        //   type: 'info',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   // iconClass:'el-icon-info',
        //   message: '<spand style="color:black;"> 已经选择的ID：'+this.currentNode.text +"</spand>"
        // });
        that.dialogFormVisible1 = true;
        that.publicScene.remove(node);//移除
      });

      scene.add(node);
      return node;
    },
    createlinkNode(nodeFrom, nodeTo,text,scene){
      // var link = new JTopo.Link(nodeFrom, nodeTo, text);//简单连线
      var link = new JTopo.CurveLink(nodeFrom, nodeTo, text);//曲线
      // var link = new JTopo.FoldLink(nodeFrom, nodeTo,text);//// 折线
      // var link = new JTopo.FlexionalLink(nodeFrom, nodeTo,text);//二次折线
      link.arrowsRadius = 10; //箭头大小
      link.lineWidth = 3; // 线宽
      link.bundleOffset = 60; // 折线拐角处的长度
      link.bundleGap = 20; // 线条之间的间隔
      link.textOffsetY = 3; // 文本偏移量（向下3个像素）
      link.dashedPattern = 5;//虚线，目前只适用于JTopo.Link，JTopo.FoldLink，JTopo.FlexionalLink，曲线不可以
      link.direction = 'horizontal';//vertical,horizontal

      link.strokeColor = '255,255,0';//线条颜色
      scene.add(link);
      return link;
    },
    createContainer(x,y,w,h,text,scene){
      var that = this;
      var container = new JTopo.Container(text);
      container.textPosition = 'Middle_Center';
      container.fontColor = '100,255,0';
      container.font = '18pt 微软雅黑';
      container.fillColor = '10,10,100';
      container.borderColor = '255,0,0';
      // container.borderRadius = 30; // 圆角



      container.setBound(x, y, w, h);

      // 流式布局（水平、垂直间隔均为10)
      // var flowLayout = JTopo.layout.FlowLayout(10, 10);
      // 网格布局(4行3列)
      var gridLayout = JTopo.layout.GridLayout(4, 3);
      //容器默认是没有固定大小的，是通过布局来实现的定位
      container.layout = gridLayout;
      let n1 = this.createNode("张三",10,10,10,10,scene);
      let n2 = this.createNode("101室",70,10,10,10,scene);
      container.add(n1);
      container.add(n2);

      container.addEventListener("dbclick", function(event){
        container.dragable = !container.dragable;//是否允许拖动。
        that.currentContainer = event.target;
        that.currentNode = undefined;
        console.info("text："+that.currentContainer.text);
        // that.$message({
        //   type: 'info',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   // iconClass:'el-icon-info',
        //   message: '<spand style="color:black;"> 已经选择的ID：'+this.currentContainer.text +"</spand>"
        // });
        that.dialogFormVisible1 = true;
        that.publicScene.remove(container);//移除
        that.publicScene.remove(n1);//移除
        that.publicScene.remove(n2);//移除
      });



      scene.add(container);
      return container;
    }
  }
}
</script>

<style scoped>

</style>



